import React, { Component } from 'react';
import { connect } from 'react-redux';
import { throwError } from '../actions';

const mapStateToProps = (state) => {
    return {
        hasError: state.throwError.hasError,
        error: state.throwError.error,
        message: state.throwError.message
    };
};

const mapDispatchToProps = (dispatch) => {
    return {
        onThrowError: (error) => dispatch(throwError(error))
    };
};

class ErrorBoundry extends Component {
    componentDidCatch(error) {
        this.props.onThrowError({ error: error, message: error.message });
    }

    render() {
        const { hasError, message } = this.props;
        return hasError ?
            (
                <div>
                    <h1>Oooops. That is not good!</h1>
                    <h2 className='washed-yellow'>{message}</h2>
                </div>
            ) :
            this.props.children;
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ErrorBoundry);



