import React, { Component } from 'react';
import Card from './Card';

class CardList extends Component {
    shouldComponentUpdate(nextProps, nextState) {
        if (this.props.robots.length === 0 && nextProps.robots.length === 0) {
            return false;
        }
        return true;
    }

    render() {
        // throw new Error('OOOOOH NOOOOO!');
        const { robots } = this.props;
        return (
            <div>
                {robots.map((user, i) => {
                    return (
                        <Card
                            key={robots[i].id}
                            id={robots[i].id}
                            name={robots[i].name}
                            email={robots[i].email}
                        />
                    );
                })}
            </div>
        );
    }
}

export default CardList;



